import { HeaderNavItem } from '@yandex/ui/Header/desktop';
import { Link, LinkProps } from 'react-router-dom';

type NavItemProps = LinkProps & {
  active?: boolean;
};

export function NavItem({ to, active, children }: NavItemProps) {
  return (
    <Link
      to={to}
      // component={(props) => <HeaderNavItem {...props} active={active} />}
    >
      {children}
    </Link>
  );
}
